@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;700&display=swap);
*{margin:0;padding:0}html{font-size:10px}body{width:100%;height:100%;margin:0;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-family:"Open Sans";font-style:normal;font-weight:normal;font-size:1.4rem;line-height:2.2rem;color:#000;box-sizing:border-box;overflow-x:hidden}code{font-family:source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace}
.header{display:flex;align-items:center;width:100%;height:6.4rem;box-shadow:0px 2px 4px rgba(0,0,0,.1);position:fixed;background:#fff}.header-title{font-size:1.6rem;font-weight:bold;margin-left:2.4rem;margin-top:.1rem}
.layout{height:92.5vh;width:100%;display:flex}.layout-todo__list{display:flex;flex-direction:row;width:100%;padding:8.8rem 0 0 7.9rem}.layout-task__add{display:flex;flex-direction:column;width:54.6%;height:100%}.layout-task__total{width:5rem;height:2rem;text-align:center;background:#fef6ff;border-radius:4px;margin-top:1.2rem;font-size:1.2rem;line-height:1.6rem;color:#550dc9}.layout-task__title{font-weight:600;font-size:1.6rem;line-height:2.2rem;color:#373737;margin-top:1.6rem}.layout-task__complete{margin-left:6.4rem;width:25.8%}.layout-task__complete .todo-item{margin:1.5rem 0 1.25rem 0}.layout-task__complete__title{font-weight:600;font-size:1.6rem;line-height:2.2rem}@media(max-width: 50rem){.layout-todo__list{flex-direction:column}.layout-todo__list-task__add{width:100%;height:50%}.layout-task__complete{margin-left:0;width:81.4%;height:50%}.layout-task__add{width:81.4%}}
.cross-out{text-decoration:line-through;-webkit-text-decoration-color:#000;text-decoration-color:#000}.todo-item{width:100%;display:flex;flex-direction:row;justify-content:space-between;align-items:center;height:4.8rem;box-shadow:0px 0px 3px rgba(0,0,0,.08);border-radius:4px;margin:.8rem 0 .8rem 0}.todo-item__check{width:1.6rem;height:1.6rem;margin:.1rem 0 0 1.2rem;border:1px solid #ececec;border-radius:4px}.todo-item__text{font-weight:normal;font-size:14px;line-height:19px;color:#373737;margin-left:.8rem;letter-spacing:.015rem}.todo-item__text__inp{height:100%;width:80%}.todo-item__toolbar{display:flex;justify-content:space-between;align-items:center;width:7.6rem;height:2rem;margin-right:1rem}.todo-item__toolbar button{height:100%;background:#fff;border:none;cursor:pointer}
.todo{display:flex;align-items:center;width:100%;height:4rem}.todo-text__inp,.todo-sub__btn{height:100%}.todo-text__inp{width:117rem;flex-grow:1;border:1px solid #ececec;border-radius:4px;padding-left:1.2rem;font-size:1.4rem;line-height:1.9rem;letter-spacing:.013rem}.todo-text__inp::-webkit-input-placeholder{padding-left:1.2rem;font-size:1.4rem;line-height:1.9rem;letter-spacing:.013rem;color:#cacaca}.todo-text__inp:-ms-input-placeholder{padding-left:1.2rem;font-size:1.4rem;line-height:1.9rem;letter-spacing:.013rem;color:#cacaca}.todo-text__inp::placeholder{padding-left:1.2rem;font-size:1.4rem;line-height:1.9rem;letter-spacing:.013rem;color:#cacaca}.todo-sub__btn{background:#550dc9;border-radius:4px;color:#fff;width:5rem;height:100%;border:none;margin-left:.8rem;cursor:pointer}@media(max-width: 50rem){.todo-text__inp{width:41rem}}
.sidebar{height:100%;width:5.5rem;background:#550dc9;box-shadow:2px 0px 4px rgba(0,0,0,.1);border-radius:0px;position:fixed;top:6.4rem}.sidebar-btn{border:none;margin-top:2.4rem;width:100%;height:3.2rem;cursor:pointer}.sidebar-btn__pic{height:100%;width:100%}
