.header {
  display: flex;
  align-items: center;
  width: 100%;
  height: 6.4rem;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
  position: fixed;
  background: #ffffff;
  &-title {
    font-size: 1.6rem;
    font-weight: bold;
    margin-left: 2.4rem;
    margin-top: 0.1rem;
  }
}

